.sectionSidebar {
  box-sizing: border-box;
  padding: 20px 15px;
  height: calc(100vh - 60px);
  max-width: 170px;
  width: 100%;
  background-color: var(--sidebar-bg);

  @media screen and (min-width: 768px) {
    max-width: 289px;
    padding: 20px 30px;
  }
}

.title {
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  line-height: 1.18;
  color: var(--sidebar-text-panel);

  text-align: center;
  margin-bottom: 10px;
}
.sidebarList {
  padding: 20px 15px;
  display: flex;
  flex-direction: column;
  gap: 15px;
  box-shadow: inset 0 0 5px rgba(255, 255, 255, 0.5);
  border-radius: 10px;
  height: 100%;

  @media screen and (min-width: 768px) {
    padding: 20px 30px;
    gap: 30px;
  }
}
.link {
  font-family: 'Montserrat', sans-serif;
  font-size: 12px;
  line-height: 1.25;
  color: var(--sidebar-text-panel);
  text-align: center;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  text-decoration: none;
  box-shadow: inset 0 0 5px rgba(255, 255, 255, 0.5);
  border-radius: 10px;
  padding: 12px 7px;

  @media screen and (min-width: 768px) {
    font-size: 16px;
    line-height: 1.18;
  }
}
.link:hover,
.link.active {
  background-color: var(--hover-btn);
}
